:root {
    --bs-border-color: var(--tc-system-stroke) !important;
    --bs-primary-rgb: 26, 170, 89;
    --bs-dark-rgb: 36, 34, 44;

    .btn-white {
        --bs-btn-hover-color: var(--tc-system-black);
    }

    .text-secondary-2 {
        color: var(--tc-black-bg-body-text);
    }
}

// background colors=

.bg-light {
    background-color: var(--tc-bg-light) !important;
}
.btn-text {
    font-family: var(--tc-btn-font-family);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    /* 14px */
    text-transform: uppercase;
}

.fill-primary {
    path {
        fill: var(--tc-theme-primary) !important;
    }

    fill: var(--tc-theme-primary) !important;
}
.stroke-primary {
    path {
        stroke: var(--tc-theme-primary) !important;
    }

    circle {
        stroke: var(--tc-theme-primary) !important;
    }
}
.contact-map {
    max-height: 700px;

    iframe {
        height: 700px;
        width: 100%;
    }
}

.map {
    -webkit-filter: grayscale(75%);
    -moz-filter: grayscale(75%);
    -ms-filter: grayscale(75%);
    -o-filter: grayscale(75%);
    filter: grayscale(75%);
}
